.main {
  flex-grow: 1;
}

.intro {
  margin-bottom: 110px;
}
@media (max-width: 991px) {
  .intro {
    padding-top: 90px;
  }
}
@media (max-width: 767px) {
  .intro {
    overflow-x: hidden;
    margin-bottom: 80px;
  }
}

.intro__wrapper {
  display: grid;
  grid-template-columns: 27fr 35fr 19fr 27fr;
  grid-template-rows: auto auto;
  grid-template-areas: "menu about about cta" "menu start feedback cta";
  gap: 50px 24px;
}
@media (max-width: 1200px) {
  .intro__wrapper {
    grid-template-columns: 250px 2fr 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas: "menu about about" "menu cta cta" "menu start feedback";
  }
}
@media (max-width: 991px) {
  .intro__wrapper {
    grid-template-columns: 250px 1fr;
    grid-template-areas: "menu about" "menu cta" "menu start" "menu feedback";
  }
}
@media (max-width: 767px) {
